.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}


body {
  overflow-y: scroll; /* This ensures that the layout doesn't shift */
  scrollbar-width: none; /* For Firefox */
}

body::-webkit-scrollbar {
  display: none; /* For Chrome, Safari, and Opera */
}


.font-source-code-pro {
  font-family: 'Source Code Pro', monospace;
}


/* project.js */
.project-item {
  perspective: 1000px; /* Adjust as needed */
}

.front-face, .back-face {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

.back-face {
  transform: rotateY(180deg);
}

/* bg-window.js */
.bg-window {
  background-color: rgb(235, 235, 235, 0.7); /* Fully opaque */
}

.bg-window-opacity {
  background-color: rgba(235, 235, 235, 0.4); /* Semi-transparent */
}


/* about.js */
@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

@keyframes fadeOut {
  from { opacity: 1; }
  to { opacity: 0; }
}

.animate-fadeIn {
  animation: fadeIn 1s ease-in forwards;
}

.animate-fadeOut {
  animation: fadeOut 0.05s ease-out forwards;
}

@keyframes blink {
  0%, 100% { opacity: 1; }
  50% { opacity: 0; }
}
.cursor-blink {
  animation: blink 1s step-start infinite;
}


@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
